import React, { Component } from 'react';
import { Switch, Route, Redirect } from 'react-router-dom';
import Tuoguan from './tuoguan';
import editGonghao from './tuoguan/edit';
import gonghaoConfig from './tuoguan/config';
import editConfig from './tuoguan/config/edit';

import Menu from './menu';
import editMenu from './menu/edit';

import Material from './material';
import editMaterial from './material/edit';


class TuoguanWrapper extends Component {
  render() {
    return (
        <Route path="/admin/gonghao" render={() =>
          <Switch>
            {/*接管公号*/}
            <Route path='/admin/gonghao/tuoguan/'>
              <Switch>
                <Route path="/admin/gonghao/tuoguan/index" component={Tuoguan} />
                <Route path='/admin/gonghao/tuoguan/create' component={editGonghao} />
                <Route path='/admin/gonghao/tuoguan/update' component={editGonghao} />
                <Route path='/admin/gonghao/tuoguan/config/index/:code' component={gonghaoConfig} />
                <Route path='/admin/gonghao/tuoguan/config/create/:code' component={editConfig} />
                <Route path='/admin/gonghao/tuoguan/config/update/:code' component={editConfig} />
                <Redirect from="/admin/gonghao/tuoguan" exact to='/admin/gonghao/tuoguan/index/' />
                <Redirect from="/admin/gonghao/tuoguan/config" exact to='/admin/gonghao/tuoguan/config/index' />
              </Switch>
            </Route>
            {/*菜单配置*/}
            <Route path='/admin/gonghao/menu/'>
              <Switch>
                <Route path="/admin/gonghao/menu/index" component={Menu} />
                <Route path='/admin/gonghao/menu/create' component={editMenu} />
                <Route path='/admin/gonghao/menu/update' component={editMenu} />
                <Redirect from="/admin/gonghao/menu" exact to='/admin/gonghao/menu/index/' />
              </Switch>
            </Route>
            <Route path='/admin/gonghao/material/'>
              <Switch>
                <Route path="/admin/gonghao/material/index" component={Material} />
                <Route path='/admin/gonghao/material/create' component={editMaterial} />
                <Route path='/admin/gonghao/material/update' component={editMaterial} />
                <Redirect from="/admin/gonghao/material" exact to='/admin/gonghao/material/index/' />
              </Switch>
            </Route>
            <Redirect from="/admin/gonghao" exact to='/admin/gonghao/tuoguan/' />
          </Switch>
        } />
    )
  }
}

export default TuoguanWrapper;